import logo from './logo.svg';
import './App.css';
import React from 'react';
import axios from 'axios';
import Navbar from './components/Navbar';
import Main from './components/Main';
import Info from './components/Info';
import Genres from './components/Genres';
import Login from './components/Login';
import {BrowserRouter as Router,Route,useParams} from 'react-router-dom';
function App() {
const [data,setData] = React.useState([]);
const [visibility,setVisibility] = React.useState('visible');
const [userData,setUserData] = React.useState({});
userData.length!=0 && console.log(userData);
// console.log(data);
React.useEffect(()=>{
  for(let i=0;i<5;i++){
    axios('https://anime5311.herokuapp.com/api/popular/'+i)
    .then((res)=>{
      // console.log(res.data);
      res.data.results.forEach((item)=>{
        setData(prev=>[...prev,item]);
      })
      
      // console.log(data)
    })
  }
},[])
  return (
    <div className="App">
      <Router>
      <Navbar data={data} setData={setData} userData={userData} setUserData={setUserData} visibility={visibility} setVisibility={setVisibility}/>
     <Genres setData={setData}/>
     <Route path='/' exact >
    <Main data={data} userData={userData} setUserData={setUserData}  visibility={visibility} setVisible={setVisibility}/>
     </Route>
     <Route path='/info/:id' exact>
        <Info userData={userData}/>
     </Route>
      </Router>
    </div>  

  );
}

export default App;
