import React from 'react'
import  './Login.css';
import GithubLogo from './Github-logo.png';
import { initializeApp } from 'firebase/app';
import CancelIcon from '@mui/icons-material/Cancel';
import { getAuth, signInWithPopup, GoogleAuthProvider,GithubAuthProvider } from "firebase/auth";
import app from '../firebase';
function Login({isVisible,setVisibility,userData,setUserData}) {
    const auth = getAuth(app);

    return (
        <div id='main-body' style={{visibility:isVisible}}>
        <button id="cancel" onClick={()=>setVisibility('hidden')}><CancelIcon sx={{color:'white'}}/></button>
            <h3 style={{color:'white'}}>Sign In or Create an Account At</h3>
            <h1>Stingrr</h1>
            <div id="buttons">
                <button id='google-button' onClick={()=>{
                    signInWithPopup(auth,new GoogleAuthProvider).then((result)=>{
                        console.log(result.user);
                        setUserData(result.user)
                        setVisibility('hidden')
                    }).catch(err=>{
                        alert(err);
                    })
                }}> <img src="https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg" alt="" />Sign In with Google</button> 
                <button id='github-button' onClick={()=>{
                    signInWithPopup(auth,new GithubAuthProvider).then(user=>{
                        console.log(user.user);
                        setUserData(user.user)
                        setVisibility('hidden')
                    }).catch(err=>{
                        alert(err);
                    })
                }}> <img src={GithubLogo} alt="" />Sign In with Github</button>
            </div>
        </div>
    )
}

export default Login
