import React from 'react'
import './Main.css';
import axios from 'axios'
import Login from './Login';
import FlipMove from 'react-flip-move';
import PlayCircleFilledIcon from '@material-ui/icons/PlayCircleFilled';
import app from '../firebase';
import {getDatabase,ref,onValue} from 'firebase/database';
import {useHistory} from 'react-router-dom';
function Main({data,userData,setUserData,visibility,setVisible}) {
    const [last_episode,setLastEP] = React.useState('');
    const db = getDatabase(app);
    let path = ref(db,`${userData.uid}/`);
    if(userData.displayName!=undefined){
        onValue(path,(snapshot)=>{
            console.log(snapshot.val())
            setLastEP(snapshot.val().last_episode_watched.toString());
        })
    }
    const history = useHistory();
    function checkLength(string){
        if(string.length > 30){
            var re = string.substr(30);
            return string.replace(re,'...')
        }
        else{
            return string;
        }
    }
    return (
        <>
        <Login isVisible={visibility} setVisibility={setVisible} userData={userData} setUserData={setUserData}/>
        {
            last_episode.length!=0 && 
            <p>Last Episode watched : {last_episode}</p>
        }
        <FlipMove enterAnimation="fade" leaveAnimation="elevator" className='main'>
        
            {
                data.length!=0?
                data.map((item)=>{
                    return(
                        
                        <div className='card' onClick={()=>{
                            history.push('/info/'+item.id)
                        }}>
                            <img src={item.image} alt="" />
                            <h4> {checkLength(item.title)} </h4>
                            <div id='visible'>
                            <PlayCircleFilledIcon id='ic'/>
                            </div>
                        </div>
                    )   
                })
                :
                <h1 style={{color:'purple'}}>Loading...</h1>
            }
            
            </FlipMove>
            </>
            )
}

export default Main
